<template>
  <div class="box">
      <!--<common-header></common-header>-->
      <common-nav :channelMRes = 'channelMRes'></common-nav>
      <div class="container clearfix">
          <div class="container-left">
            <transition name="">
              <!--<keep-alive>-->
                <router-view :channelMRes = 'channelMRes'></router-view>
              <!--</keep-alive>-->
            </transition>
          </div>
          <component :is="RightComponent"></component>
      </div>
      <!--<common-footer></common-footer>-->
  </div>
</template>

<script>
  import CommonHeader from '../components/CommonHeader'
  import CommonFooter from '../components/CommonFooter'
  import CommonNav from '../components/CommonNav'
  import MainRight from '@/modules/container-right/MainRight'
  import {channelMList} from '@/api/info'
    export default {
        name: "info-main",
        components:{
          CommonHeader,
          CommonFooter,
          CommonNav,
          MainRight
        },
      data(){
          return {
            channelMRes:[],
            RightComponent:''
          }
      },
      mounted(){
          this.judgeShowRight()
          this.initMChannel()
      },
      watch:{
          '$route.path'(to,from){
            this.judgeShowRight()
          }
      },
      methods:{
        initMChannel(){
          channelMList({channelMDesc:'CPY'}).then((res)=>{
            let data
            if(res.status==0){
              data =  res.datas
              this.channelMRes = data
            }
          })
        },
        judgeShowRight(){
          switch(this.$route.path){
            case '/properties':
              this.RightComponent = null
                  break;
            default:
              this.RightComponent= MainRight
          }
        }
      }
    }
</script>

<style scoped>
.container{
  background: #fff;
  width: 1180px;
  margin: 0 auto;
}
  .container-left{
    display: inline-block;
    /*width: 840px;*/
  }



.slide-left-enter-active, .slide-left-leave-active {
  transition: all .2s cubic-bezier(0, 1.2, 1, 0.5);
  opacity: .5;
  transform: translate3d(2em, 0, 0);
}

.slide-left-enter, .slide-left-leave-active {
  opacity: .3;
  transform: translate3d(2em, 0, 0);
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
</style>



